import React, { Component } from 'react';
import './Guilin.css';

class Guilin extends Component{
    constructor(props){
        super(props);
        this.state = {
            image: []
        }
    }
    componentDidMount(){
        document.title = 'Guilin | FTL';
    }

    zoomImage(model) {
        var modal = document.getElementById('myModal');
        let modalImg = document.getElementById("myImg");
        modal.style.display = 'block';
        modalImg.src = require('./../../img/guilin/image' + model + '.JPG');
      }
    
    imgClose = () => {
        var modal = document.getElementById('myModal');
        modal.style.display = 'none';
    }

    imagerender = (data) => {
        return (
            <div key={data} className='col-lg-3 col-md-4 col-sm-4 col-6' style={{marginBottom: 10}}>
                <div className='sample-show'>
                <img className='sample-pic' src={require('./../../img/guilin/image' + data + '.JPG')} alt='asd' onClick={() => this.zoomImage(data)} />
                </div>
            </div>
        );
    }

    render(){
        let item = [];
        for (let i = 1; i < 58; i++){
            item.push(i);
        }
        return(
            <div>
                <div className='guilin-hero'></div>
                <div className='container' style={{marginTop: 30}}>
                    <div className='row'>
                        {item.map(this.imagerender)}
                    </div>
                </div>

                <div id="myModal" className="modal">
                    <span className="close" onClick={()=>this.imgClose()}>&times;</span>
                    <img className="modal-content" id="myImg" alt='modal' onClick={()=>this.imgClose()}/>
                </div>
            </div>
        );
    }

}

export default Guilin;